'use client'

import React from 'react'
import { useTranslation } from 'react-i18next'

import { initClientI18n } from '~/lib/i18n/init'

export default function MyClient({ locale }: { locale: string }) {
  // инициализируем i18n сразу, без useEffect
  initClientI18n(locale)

  const { t } = useTranslation('common')

  return <p>{t('language')}</p>
}
